// TICKETS
.ticket {
  position: relative;
  box-sizing: border-box;
  width: 300px;
  height: 450px;
  margin: 50px auto 0;
  padding: 20px 10px;
  border-radius: 10px;
  background: #fbfbfb;
  box-shadow: 2px 2px 15px 0px #ab9b0d;
  display: inline-table;
  margin: 1.5rem;
  margin-top: 40px;
  transition: 0.2s;

  &:hover {
    background-color: rgba(255, 255, 255, 0.93);
    cursor: pointer;

    button {
      color: #212529;
      background-color: #e0a800;
      border-color: #d39e00;
    }

    // font-size: 30px;
  }

  &:before,
  &:after {
    content: "";
    position: absolute;
    left: 5px;
    height: 6px;
    width: 290px;
  }

  &:before {
    top: -5px;
    background: radial-gradient(circle, transparent, transparent 50%, #FBFBFB 50%, #FBFBFB 100%) -7px -8px / 16px 16px repeat-x,

  }

  &:after {
    bottom: -5px;
    background: radial-gradient(circle, transparent, transparent 50%, #FBFBFB 50%, #FBFBFB 100%) -7px -2px / 16px 16px repeat-x,
  }
}

