// Timeline
$timeline-color: $red;


// Mixins and Placeholders
%clearfix {

  &:after,
  &:before {
    content: '';
    display: block;
    width: 100%;
    clear: both;
  }
}

@mixin prefix($prop, $val) {

  @each $prefix in '-webkit-',
  '-moz-',
  '-ms-',
  '' {
    #{$prefix}#{$prop}: $val;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}


